body {
  font-family: "Segoe UI", Tahoma, sans-serif;
  background: linear-gradient(135deg, #0d0d0d, #1a1a1a);
  color: #fff;
  margin: 0;
  padding: 0;
  text-align: center;
  direction: rtl;
}

.container {
  margin: 50px auto;
  max-width: 750px;
  background: #1c1c1c;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 0 25px #0d6efd;
}

h1 {
  color: #0d6efd;
  margin-bottom: 20px;
  font-size: 28px;
}

button {
  background: #ff0055;
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  background: #ff3366;
}

.card {
  margin-top: 25px;
  padding: 20px;
  border-radius: 12px;
  background: #2c2c2c;
  text-align: right;
  border: 1px solid #444;
}

.card p {
  margin: 8px 0;
}

.progress-bar {
  height: 15px;
  background: #333;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 15px;
}

.progress-bar div {
  height: 100%;
  background: #0d6efd;
  border-radius: 10px 0 0 10px;
  transition: width 0.5s;
}

.advice {
  margin-top: 15px;
  font-size: 15px;
  color: #ffcc00;
}

footer {
  margin-top: 30px;
  font-size: 14px;
  color: #aaa;
}

footer .social a {
  color: #0d6efd;
  text-decoration: none;
  margin: 0 8px;
  font-size: 24px;
  transition: 0.3s;
}

footer .social a:hover {
  color: #ff0055;
}
